import React, {Component} from 'react';
import '../style/Ranker.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
export default class Ranker extends Component{
    render(){
        let sizePixel = this.props.sizePixel || 48;
        let score = this.props.score || 0;
        let max = this.props.max || 5;
        let index = 1;
        let result = [];

        let flip = this.props.flipHalf ? "horizontal" : "";

        for (let i = 0; i<Math.floor(score); i++){
            result.push(<span className="rankerPoint" key={index++}><FontAwesomeIcon icon="star"/></span>);
        }
        if(score % 1.0 === 0){
            for(let i = 0; i<max-Math.floor(score); i++){
                result.push(<span className="rankerPoint" key={index++}><FontAwesomeIcon icon={["far", "star"]}/></span>);
            }
        } else {
            result.push(<span className="rankerPoint" key={index++}><FontAwesomeIcon icon="star-half-alt" flip={flip}/></span>);
            for(let i = 0; i<max-Math.floor(score)-1; i++){
                result.push(<span className="rankerPoint" key={index++}><FontAwesomeIcon icon={["far", "star"]}/></span>);
            }
        }
        return(
            <div className="ranker d-flex justify-content-center" style={{fontSize: sizePixel}}>
                <span>
                    {result}
                </span>
            </div>
        );
    }
}