import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import { Collapse, Navbar, NavbarBrand, NavbarToggler, Nav, NavItem, Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import logo from '../logo.svg';
import enFlag from '../assets/img/gb.svg';
import itFlag from '../assets/img/it.svg';
import ilFlag from '../assets/img/il.svg';

export default class CustomNavbar extends Component{
    constructor(props){
        super(props);
        this.toggle = this.toggle.bind(this);
        this.toggleDropdown = this.toggleDropdown.bind(this);
        this.setLanguage = this.setLanguage.bind(this);
        this.setLanguageEn = this.setLanguageEn.bind(this);
        this.setLanguageIt = this.setLanguageIt.bind(this);
        this.setLanguageIl = this.setLanguageIl.bind(this);

        this.handleHamburgerButtonClick = this.handleHamburgerButtonClick.bind(this);
        
        this.state = {
            isOpen: false,
            hamburgerButtonClicked: false, 
            dropdownOpen: false,
            titles: {
                about: {
                    "en": "About me",
                    "il": "◊ß◊¶◊™ ◊¢◊ú◊ô◊ô",
                    "it": "Chi sono"
                },
                experience: {
                    "en": "Experience",
                    "il": "◊†◊ô◊°◊ô◊ï◊ü",
                    "it": "Esperienza"
                },
                softskills: {
                    "en": "Soft skills",
                    "il": "◊û◊ô◊ï◊û◊†◊ï◊ô◊ï◊™ ◊®◊õ◊ï◊™",
                    "it": "Competenze trasversali"
                },
                language: {
                    "en": "Language",
                    "il": "◊©◊§◊î",
                    "it": "Lingua"
                },
                portfolio: {
                    "en": "Portfolio",
                    "it": "Portfolio",
                    "il": "◊§◊ï◊®◊ò◊§◊ï◊ú◊ô◊ï"
                },
                hobbies: {
                    "en": "Hobbies",
                    "it": "Hobbies",
                    "il": "◊™◊ó◊ë◊ô◊ë◊ô◊ù"
                }
            }
        }
    }

    async handleHamburgerButtonClick(){
        if(!this.state.hamburgerButtonClicked){
            await this.setState({
                hamburgerButtonClicked: true
            });
        }
        this.toggle();
    }

    toggle(){
        if(this.state.hamburgerButtonClicked){
            this.setState({
                isOpen: !this.state.isOpen
            });
        }
    }

    toggleDropdown(){
        this.setState({
            dropdownOpen: !this.state.dropdownOpen
        });
    }

    setLanguage(language){
        this.props.setState({
            language: language
        });
        this.toggle();
    }

    setLanguageEn(){
        this.setLanguage("en");
    }

    setLanguageIt(){
        this.setLanguage("it");
    }

    setLanguageIl(){
        this.setLanguage("il");
    }

    render(){
        let selectedLanguage = (["en", "it", "il"].includes(this.props.language)) ? this.props.language : "en";
        let dir = (this.props.language === "il") ? "rtl" : "ltr";
        let autoMargin = (dir === "ltr") ? "ml-auto" : "mr-auto";
        let textAlignDirection = (dir === "ltr") ? "right" : "left";
        let leftFlag = selectedLanguage === "en" ? <img id="flag" src={enFlag}/> : selectedLanguage === "it" ? <img id="flag" src={itFlag} /> : selectedLanguage === "il" ? <img id="flag" src={ilFlag} /> : null;
        return(
            <Navbar className="navbar-custom" expand="md" dir={dir} fixed="top" dark>
                <NavbarBrand>
                    <NavLink to="/about">
                        <img src={logo} height="50" alt="SB.dev logo"/>
                    </NavLink>
                </NavbarBrand>
                <NavbarToggler onClick={this.handleHamburgerButtonClick}/>
                <Collapse isOpen={this.state.isOpen} navbar>
                <Nav className={autoMargin} navbar style={{textAlign: textAlignDirection}}>
                    <NavItem>
                        <NavLink to="/about" onClick={this.toggle} dir={dir} className="customNavbarLink" activeClassName="customNavbarActiveLink">{this.state.titles.about[selectedLanguage]}</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/experience" onClick={this.toggle} dir={dir} className="customNavbarLink" activeClassName="customNavbarActiveLink">{this.state.titles.experience[selectedLanguage]}</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/softskills" onClick={this.toggle} dir={dir} className="customNavbarLink" activeClassName="customNavbarActiveLink">{this.state.titles.softskills[selectedLanguage]}</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/portfolio" onClick={this.toggle} dir={dir} className="customNavbarLink" activeClassName="customNavbarActiveLink">{this.state.titles.portfolio[selectedLanguage]}</NavLink>
                    </NavItem>
                    <NavItem>
                        <NavLink to="/hobbies" onClick={this.toggle} dir={dir} className="customNavbarLink" activeClassName="customNavbarActiveLink">{this.state.titles.hobbies[selectedLanguage]}</NavLink>
                    </NavItem>
                    <NavItem>
                        <Dropdown className="navbar-dropdown" isOpen={this.state.dropdownOpen} toggle={this.toggleDropdown}>
                            <DropdownToggle caret>{leftFlag} {this.state.titles.language[this.props.language]} </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem active={this.props.language === "en"} onClick={this.setLanguageEn} dir="ltr">English</DropdownItem>
                                <DropdownItem active={this.props.language === "it"} onClick={this.setLanguageIt} dir="ltr">Italian</DropdownItem>
                                <DropdownItem active={this.props.language === "il"} onClick={this.setLanguageIl} dir="rtl">◊¢◊ë◊®◊ô◊™</DropdownItem>
                            </DropdownMenu>
                        </Dropdown>
                    </NavItem>
                </Nav>
                </Collapse>
            </Navbar>
        );
    }
}