import React, {Component} from 'react';
import '../style/Hobbies.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {Row, Col, Button, Card} from 'react-bootstrap';
import ReactCardFlip from 'react-card-flip';

export default class Hobbies extends Component{

    constructor(props){
        super(props);
        this.handleClickCooking = this.handleClickCooking.bind(this);
        this.handleClickTravelling = this.handleClickTravelling.bind(this);
        this.state = {
            cookingIsFlipped: false,
            myHobbiesTitle: {
                "en": "My hobbies are",
                "it": "I miei hobby sono",
                "il": "◊î◊™◊ó◊ë◊ô◊ë◊ô◊ù ◊©◊ú◊ô"
            },
            flipButtonText: {
                "en": "Flip me!",
                "it": "Girami!",
                "il": "◊î◊§◊ï◊ö ◊ê◊ï◊™◊ô!"
            },
            cooking: {
                "en": ["Cooking", "(but mostly eating!)"],
                "it": ["Cucinare", "(e mangiare, ovviamente!)"],
                "il": ["◊ú◊ë◊©◊ú", "(◊ê◊ë◊ú ◊ë◊¢◊ô◊ß◊® ◊ú◊ê◊õ◊ï◊ú!)"]
            },
            cookingDescription: {
                "en": "I like to experiment with dishes from all over the world.",
                "it": "Mi piace sperimentare con le cucine di tutto il mondo.",
                "il": "◊ê◊†◊ô ◊ê◊ï◊î◊ë ◊ú◊î◊™◊†◊°◊ï◊™ ◊¢◊ù ◊û◊ê◊õ◊ú◊ô◊ù ◊û◊õ◊ú ◊î◊¢◊ï◊ú◊ù."
            },
            travelling: {
                "en": ["Travelling", "around the world"],
                "it": ["Viaggiare", "in tutto il mondo"],
                "il": ["", ""]
            },
            travellingDescription: {
                "en": "I like to see new places and meet new people.",
                "it": "Mi piace vedere posti nuovi e incontrare persone diverse.",
                "il": ""
            }
        }
    }

    handleClickCooking(e){
        e.preventDefault();
        this.setState({
            cookingIsFlipped: !this.state.cookingIsFlipped
        });
    }

    handleClickTravelling(e){
        e.preventDefault();
        this.setState({
            travellingIsFlipped: !this.state.travellingIsFlipped
        });
    }

    render(){
        let display = this.props.hidden ? {display: "none"} : {display: "block"};
        let languageSelected = (["en", "it", "il"].includes(this.props.language)) ? this.props.language : "en";
        let dir = (this.props.language === "il") ? "rtl" : "ltr";
        let alignment = (this.props.language === "il") ? "text-right" : "text-left";
        let flipButtonText = this.state.flipButtonText[languageSelected];

        return(
            <div style={display} dir={dir} id="hobbies">
                <Row className="mt-5 justify-content-center">
                    <Col xs={10}>
                        <h1 className={"h1 text-center"} dir={dir}>{this.state.myHobbiesTitle[languageSelected]}</h1>
                    </Col>
                </Row>
                <Row className="mt-5 justify-content-center">
                    <Col xs={10} md={5}>
                        <ReactCardFlip isFlipped={this.state.cookingIsFlipped} flipDirection="horizontal" infinite={true}>
                            <Card key="front" className="cardFront">
                                <Card.Body className={alignment} dir={dir}>
                                        <h1>{this.state.cooking[languageSelected][0]}<br/>{this.state.cooking[languageSelected][1]}</h1>
                                </Card.Body>
                                <Card.Footer>
                                    <Button block className="purpleButtonOutline" dir={dir} onClick={this.handleClickCooking}>{flipButtonText}</Button>
                                </Card.Footer>
                            </Card>
                            <Card key="back" id="cookingBack" className="cardBack">
                                <Card.ImgOverlay>
                                    <Card.Body>
                                        <Card.Text className={alignment} dir={dir}>
                                            {this.state.cookingDescription[languageSelected]}
                                        </Card.Text>
                                    </Card.Body>
                                    <Card.Footer>
                                        <Button block className="purpleButtonOutline" dir={dir} onClick={this.handleClickCooking}>{flipButtonText}</Button>
                                    </Card.Footer>
                                </Card.ImgOverlay>
                            </Card>
                        </ReactCardFlip>
                    </Col>
                    <Col xs={10} md={5}>
                        <ReactCardFlip isFlipped={this.state.travellingIsFlipped} flipDirection="horizontal" infinite={true}>
                            <Card key="front" className="cardFront">
                                <Card.Body className={alignment} dir={dir}>
                                        <h1>{this.state.travelling[languageSelected][0]}<br/>{this.state.travelling[languageSelected][1]}</h1>
                                </Card.Body>
                                <Card.Footer>
                                    <Button block className="purpleButtonOutline" dir={dir} onClick={this.handleClickTravelling}>{flipButtonText}</Button>
                                </Card.Footer>
                            </Card>
                            <Card key="back" id="travellingBack" className="cardBack">
                                <Card.ImgOverlay>
                                    <Card.Body>
                                        <Card.Text className={alignment} dir={dir}>
                                            {this.state.travellingDescription[languageSelected]}
                                        </Card.Text>
                                    </Card.Body>
                                    <Card.Footer>
                                        <Button block className="purpleButtonOutline" dir={dir} onClick={this.handleClickTravelling}>{flipButtonText}</Button>
                                    </Card.Footer>
                                </Card.ImgOverlay>
                            </Card>
                        </ReactCardFlip>
                    </Col>
                </Row>
            </div>
        );
    }
}