import React,{Component} from 'react';
import './style/App.css';
import {HashRouter, Route, Switch, Redirect} from 'react-router-dom';
import CustomNavbar from './components/CustomNavbar';
import About from './components/About';
import Experience from './components/Experience';
import Softskills from './components/Softskills';
import Portfolio from './components/Portfolio';
import Hobbies from './components/Hobbies';
import { library } from '@fortawesome/fontawesome-svg-core';
import {faStar as fasStar, faStarHalfAlt as fasStarHalf, faAward as fasAward } from '@fortawesome/free-solid-svg-icons';
import {faStar as farStar } from '@fortawesome/free-regular-svg-icons';
import { fab } from '@fortawesome/free-brands-svg-icons';
import Footer from './components/Footer';

export default class App extends Component {
  constructor(props){
    super(props);

    this.state = {
      language: "en"
    }
    library.add(fasStar, fasStarHalf, farStar, fasAward, fab);
  }

  render(){
    let language = this.state.language;
    const RedirectAbout = () => <Redirect to="/about" />
    const NoMatch = () => <h1 style={{paddingTop: "200px"}}>404 Page not found!!!</h1>;
    return (
      <div className="App">
        <HashRouter basename={process.env.PUBLIC_URL}>
         <CustomNavbar language={language} setState={((state, callback) => {this.setState(state, callback)})}/>
          <div className="container">
            <Switch>
              <Route exact path="/" component={RedirectAbout}></Route>
              <Route exact path="/about" render={props => <About language={language}/>}></Route>
              <Route exact path="/experience" render={props => <Experience language={language}/>}></Route>      
              <Route exact path="/softskills" render={props => <Softskills language={language}/>}></Route>      
              <Route exact path="/portfolio" render={props => <Portfolio language={language}/>}></Route>
              <Route exact path="/hobbies" render={props => <Hobbies language={language}/>}></Route>
              <Route component={NoMatch}></Route>
            </Switch>
          </div>
          <Footer/>
        </HashRouter  >
      </div>
    );
  }
}

