import React, {Component} from 'react';

export default class Portfolio extends Component{
    constructor(props){
        super(props);
    }

    render(){
        let display = this.props.hidden ? {display: "none"} : {display: "block"};
        return(
            <div style={display}>
                <h1>Portfolio</h1>
            </div>
        );
    }
}